

.main_header{
  font-size: 70px;
  font-family:"Segoe UI";
  
}

.wind_header{
  font-size: 20px;
  font-family:"Segoe UI"; 
}

.weather_bar{
  width:20px;
  height:80px;
  background-color: white;
  border-radius: 50px;
}

.blink{
  font-size: 70px;
  font-family:"Segoe UI";
  animation: blinking 1.5s infinite;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes blinking {
  20%{
    opacity: 0;
  }
  80%{
    opacity: 1;
  }
}
